import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { api } from "../services/api"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { Store, StoreModel } from "./Store"

export const StoreStoreModel = types
    .model("StoreStore")
    .props({
        stores: types.array(StoreModel),
        storeLocation: "",
        pageIndex: 0,
        pageSize: 6,
        pageCount: 0
    })
    .actions(withSetPropAction)
    .actions((store) => ({
        async fetchStores(authToken: string) {
            const serviceName = 'getAllTbStore'
            const query = `query{${serviceName}(request:{page_size: ${store.pageSize}, page_index:${store.pageIndex}, filter: { ${store.storeLocation? 'store_location:'+store.storeLocation+'' : ''}}}){ status,error,count,page_count,list_data{id,store_name,store_location,location{id,location_name}}}}`
            let response = await api.graphqlStore(query, authToken)
            if (response.kind === "ok") {
                store.setProp("stores", response.stores)
            } else {
                console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
            }
        },
        setStoreLocation(value?: string) {
            store.storeLocation = value
        }
    }))
    .views((store) => ({
        get storeComboboxOptions() {
            return store.stores.map((item) => ({
                label: item.store_name,
                value: item.id
            }))
        }
        // get locationsForList() {
        //     return store.favoritesOnly ? store.favorites : store.locations
        // }
    }))

    export interface StoreStore extends Instance<typeof StoreStoreModel> {}
    export interface StoreStoreSnapshotOut extends SnapshotOut<typeof StoreStoreModel> {}
    export interface StoreStoreSnapshotIn extends SnapshotIn<typeof StoreStoreModel> {}
