import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { Store, StoreModel } from "./Store"
import { Invoice } from "./Invoice"
import { Kwitansi } from "./Kwitansi"

/**
 * This represents an delnote of React Native Radio.
 */
export const DelnoteModel = types
  .model("Delnote")
  .props({
    id: types.identifier,
    nomor_surat: "",
    store_id: "",
    store: types.frozen<Store>(),
    invoice: types.frozen<Invoice>(),
    receipt: types.frozen<Kwitansi>(),
    quantity: types.number,
    delivery_year: types.number,
    delivery_month: types.number,
    delivery_date: types.number,
    total_case: types.number,
    price: "",
    created_dt: "",
    updated_dt: "",
    created_by: "",
    // updated_by: "",
    version: types.number,
  })
  .actions(withSetPropAction)
  .views((delnote) => ({
    get isInvoiceCreated() {
      return delnote.invoice?.nomor_invoice !== undefined
    },
    
    get parsedTitleAndSubtitle() {
      return { title: delnote.nomor_surat?.trim(), subtitle: delnote.store.store_name?.trim() }
    },
    get storeLocationName() {
      return delnote.store.location.location_name
    },
    get deliveryYearMonth() {
      // pad month with 0
      let delivery_month = `${delnote.delivery_month < 10 ? '0'+delnote.delivery_month:delnote.delivery_month}`
      let delivery_date = `${delnote.delivery_date < 10 ? '0'+delnote.delivery_date:delnote.delivery_date}`
      return `${delnote.delivery_year}-${delivery_month}-${delivery_date}`
    }
    // get store() {
    //   return delnote.store_id
    // }
  }))

export interface Delnote extends Instance<typeof DelnoteModel> {}
export interface DelnoteSnapshotOut extends SnapshotOut<typeof DelnoteModel> {}
export interface DelnoteSnapshotIn extends SnapshotIn<typeof DelnoteModel> {}

