import { Instance, SnapshotOut, types } from "mobx-state-tree"

export const FilteringStoreModel = types
  .model("FilteringStore")
  .props({
    deliveryNoteNumber: "",
    storeId: "",
    locationId: "",
    deliveryYear: "",
    deliveryMonth: "",
    deliveryDate: "",
  })
  .views((store) => ({
  }))
  .actions((store) => ({
    setDeliveryNoteNumber(value?: string) {
      store.deliveryNoteNumber = value
    },
    setStoreId(value?: string) {
      store.storeId = value
    },
    setLocationId(value?: string) {
      store.locationId = value
    },
    setDeliveryYear(value?: string) {
      store.locationId = value
    },
    setDeliveryMonth(value?: string) {
      store.deliveryMonth = value
    },
    setDeliveryDate(value?: string) {
      store.deliveryDate = value
    },
  }))
  .preProcessSnapshot((snapshot) => {
    // remove sensitive data from snapshot to avoid secrets
    // being stored in AsyncStorage in plain text if backing up store
    console.log('preProcessSnapshot snapshot', snapshot)
    const { ...rest } = snapshot // eslint-disable-line @typescript-eslint/no-unused-vars
    // const { authToken, authPassword, ...rest } = snapshot // eslint-disable-line @typescript-eslint/no-unused-vars

    // see the following for strategies to consider storing secrets on device
    // https://reactnative.dev/docs/security#storing-sensitive-info

    return rest
  })

export interface FilteringStore extends Instance<typeof FilteringStoreModel> {}
export interface FilteringStoreSnapshot extends SnapshotOut<typeof FilteringStoreModel> {}

