import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { Receipt } from "./Receipt"

/**
 * This represents an delnote of React Native Radio.
 */
export const InvoiceModel = types
  .model("Invoice")
  .props({
    id: types.identifier,
    nomor_invoice: "",
    receipt: types.frozen<Receipt>(),
  })
  .actions(withSetPropAction)
  .views((store) => ({
    get isReceiptCreated() {
      return store.receipt?.nomor_kwitansi !== undefined
    },
    // get parsedTitleAndSubtitle() {
    //   return { title: delnote.nomor_surat?.trim(), subtitle: "" }
    // },
    // get store() {
    //   return delnote.store_id
    // }
  }))

export interface Invoice extends Instance<typeof InvoiceModel> {}
export interface InvoiceSnapshotOut extends SnapshotOut<typeof InvoiceModel> {}
export interface InvoiceSnapshotIn extends SnapshotIn<typeof InvoiceModel> {}

